import React, { Component } from 'react'
import Helmet from 'react-helmet'
import image_404 from '../assets/images/404.jpg'

class COMMON_HELMET extends Component {
  constructor(props) {
    super(props)
  }
  render() {
    let site_title = 'ALISハッカー部'
    let subdomain = ''
    if (process.env.WAVES_NETWORK === 'TESTNET') {
      site_title = 'AHT TESTNET'
      subdomain = 'testnet.'
    }

    let metas = []
    let title = this.props.title || `404ページ | ${site_title}`
    let desc = this.props.desc
    if (desc == undefined && desc != '') {
      desc =
        '史上初！マミぃさんに300AHTお支払いで描いていただいたハッカー部のイラストです。'
    }
    let image =
      this.props.image || `https://${subdomain}alishackers.club${image_404}`
    metas.push({ name: 'twitter:card', content: 'summary_large_image' })
    metas.push({ name: 'twitter:site', content: '@alishackers' })
    metas.push({
      name: 'twitter:image',
      content: image,
    })
    metas.push({ name: 'twitter:description', content: desc })
    metas.push({ name: 'twitter:title', content: title })
    metas.push({ name: 'og:title', content: title })
    metas.push({
      name: 'og:image',
      content: image,
    })
    metas.push({ name: 'og:description', content: desc })
    metas.push({ name: 'description', content: desc })
    metas.push({
      name: 'keywords',
      content:
        'ALIS, ALISハッカー部, ハッカー部, ALIS HackerToken, 独自経済圏, トークンエコノミー, 暗号通貨, cryptocurrency, ハッカー部トークン, WAVES, 仮想通貨',
    })
    let links = []
    if (this.props.links != undefined) {
      for (let link of this.props.links) {
        links.push({
          rel: 'stylesheet',
          type: 'text/css',
          href: link,
        })
      }
    } else {
      links = [
        {
          rel: 'stylesheet',
          type: 'text/css',
          href: '/tabler/css/tabler.min.css',
        },
        {
          rel: 'stylesheet',
          type: 'text/css',
          href: '/css/common.css',
        },
      ]
    }
    return <Helmet title={title} link={links} meta={metas} />
  }
}

export default COMMON_HELMET
